import paper from "paper";
let waveform = [];
let startX = 400, endX = startX + 300;
const sampleRate = 22050;
let audioCtx;
let audioSource;
let hasAudioContextStarted = false;
const UP = "up";
const RIGHT = "right";
const LEFT = "left";
const DOWN = "down";
const sine = () => Array.from({ length: paper.view.viewSize.width }, (_, x) => new paper.Point(x, Math.sin(x / 15) / 2));
const flat = () => Array.from({ length: paper.view.viewSize.width }, (_, x) => new paper.Point(x, 0));
const noise = () => Array.from({ length: paper.view.viewSize.width }, (_, x) => new paper.Point(x, (Math.random() * 2 - 1) / 2));
const paperCanvas = (canvas) => {
    paper.setup(canvas);
    const tool = new paper.Tool();
    const instructionText = new paper.PointText({
        point: new paper.Point(paper.view.viewSize.width / 10, paper.view.viewSize.height / 10),
        content: "Press the canvas to draw new waveforms.\nUse the arrow keys to move the sample slice",
        justification: "left",
        fontSize: 15,
    });
    const path = new paper.Path({ strokeColor: "black" });
    let startLinePath = new paper.Path.Line(new paper.Point(startX, 0), new paper.Point(startX, paper.view.viewSize.height));
    startLinePath.strokeColor = new paper.Color("red");
    let endLinePath = new paper.Path.Line(new paper.Point(endX, 0), new paper.Point(endX, paper.view.viewSize.height));
    endLinePath.strokeColor = new paper.Color("red");
    const drawSegmentsFromPoints = (points) => {
        path.removeSegments();
        points.forEach((point) => (waveform[point.x] = point));
        waveform.forEach((point) => path.add(new paper.Point(point.x, map(point.y, -1, 1, 0, paper.view.viewSize.height))));
    };
    tool.onKeyDown = (event) => {
        const speed = 3;
        if (event.key === RIGHT) {
            startX += speed;
            endX += speed;
        }
        else if (event.key === LEFT) {
            startX -= speed;
            endX -= speed;
        }
        else if (event.key === UP) {
            startX -= speed;
            endX += speed;
        }
        else if (event.key === DOWN) {
            startX += speed;
            endX -= speed;
        }
        // TODO: feels unneccessary to remove and redraw everything
        startLinePath.removeSegments();
        startLinePath = new paper.Path.Line(new paper.Point(startX, 0), new paper.Point(startX, paper.view.viewSize.height));
        startLinePath.strokeColor = new paper.Color("red");
        endLinePath.removeSegments();
        endLinePath = new paper.Path.Line(new paper.Point(endX, 0), new paper.Point(endX, paper.view.viewSize.height));
        endLinePath.strokeColor = new paper.Color("red");
        beep();
    };
    tool.onKeyUp = (event) => {
        if ([UP, RIGHT, DOWN, LEFT].includes(event.key)) {
            beep();
            if (instructionText) {
                instructionText.visible = false;
            }
        }
    };
    let points = [];
    paper.view.onMouseDown = (event) => (points = []);
    paper.view.onMouseDrag = (event) => {
        // TODO: manual smoothing of points that are in between,
        // i.e. smooth idx 2 for points with x values [0, 1, 3, 5]
        points.push(event.point);
        const normalizedPoints = points.map((point) => new paper.Point(point.x, map(point.y, 0, paper.view.viewSize.height, -1, 1)));
        drawSegmentsFromPoints(normalizedPoints);
    };
    paper.view.onMouseUp = (event) => {
        // path.simplify();
        // path.smooth({ type: "continuous" });
        beep();
    };
    drawSegmentsFromPoints(sine());
    // set up buttons
    const clearButton = document.querySelector("#clear");
    if (clearButton) {
        clearButton.addEventListener("click", () => {
            drawSegmentsFromPoints(flat());
            beep();
        });
    }
    const playButton = document.querySelector("#play");
    if (playButton) {
        playButton.addEventListener("click", () => {
            if (!hasAudioContextStarted) {
                audioCtx = new window.AudioContext({ sampleRate: sampleRate });
                hasAudioContextStarted = true;
                console.log("AudioContext started");
                beep();
            }
        });
    }
};
const beep = () => {
    if (!hasAudioContextStarted)
        return;
    if (audioSource) {
        audioSource.stop();
    }
    audioSource = audioCtx.createBufferSource();
    audioSource.onended = () => {
        console.log("AudioSource finished", new Date().toLocaleString("no-NO"));
    };
    const durationSeconds = 1;
    const channels = 1;
    let frameCount = audioCtx.sampleRate * durationSeconds;
    let audioBuffer = audioCtx.createBuffer(channels, frameCount, audioCtx.sampleRate);
    const samples = waveform.map((point) => point.y).slice(startX, endX);
    // const mirroredSamples = samples.concat([...samples].reverse());
    for (let channel = 0; channel < channels; channel++) {
        let nowBuffering = audioBuffer.getChannelData(channel);
        for (let i = 0; i < frameCount; i++) {
            nowBuffering[i] = samples[i % samples.length];
        }
    }
    audioSource.loop = false;
    audioSource.buffer = audioBuffer;
    audioSource.connect(audioCtx.destination);
    audioSource.start();
};
const printViewDimensions = () => {
    console.log("W x H:", paper.view.viewSize.width, "x", paper.view.viewSize.height);
};
const map = (value, a, b, c, d) => ((value - a) * (d - c)) / (b - a) + c;
export default paperCanvas;
